.footer {
  &__container {
    max-width: 1200px;
    margin: 0 auto;

    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__logo {
    margin: 142px 0 65px;
    transition: 400ms;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__arrow {
    position: absolute;
    right: 80px;
    top: 150px;

    transition: 400ms;

    &:hover {
      transform: scale(1.2);
    }
  }

  &__socials {
    margin-bottom: 36px;
  }

  &__copyright {
    margin-bottom: 100px;
    color: $color-gray;
  }
}

@media (max-width: $phone-screen) {
  .footer {
    &__logo {
      display: none;
    }

    &__arrow {
      right: 30px;
      top: -50px;
    }

    &__copyright {
      max-width: 260px;
      margin-bottom: 50px;

      text-align: center;
    }
  }
}
